body {
  font-family: 'Montserrat', sans-serif !important;
}

.MuiTypography-h1, .MuiTypography-h2, .MuiTypography-h3, .MuiTypography-h4, .MuiTypography-h5, .MuiTypography-h6, .MuiTypography-body2, .MuiTypography-body1, .MuiButton-root, .MuiButton-caption {
  font-family: 'Montserrat', sans-serif !important;
}

