hr {
  border: 0;
  height: 1px !important;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0)) !important;
}

textarea:hover, input:hover, textarea:active, input:active, textarea:focus, input:focus, button:focus, button:active, button:hover, label:focus, .btn:active, .btn.active {
  outline: 0px !important;
  -webkit-appearance: none;
  box-shadow: none !important;
}

.header {
  background: url(./Static/Images/banner4.jpg) no-repeat center center;
  padding: 0;
  -webkit-background-size: cover;
  background-size: cover;
  -moz-background-size: cover;
  background-attachment: fixed;
  color: #fff;
}

.header .overlay {
  background-color: rgba(3, 3, 3, 0.4);
  min-height: 700px;
}

/* Navbar */

.mobileNavigationbar {
  background-color: black !important;
  color: white;
  width: 100%;
  height: auto;
  /* top: 0; */
  position: fixed !important;
}

.transparent-nav {
  background-color: transparent !important;
  color: #fff !important;
  position: absolute !important;
  box-shadow: none !important;
  margin-top: 12px !important;
}

.fixed-nav {
  background-color: #fff !important;
  color: #000 !important;
  font-weight: 500 !important;
  position: fixed !important;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  margin-top: 0 !important;
}

.logo-box {
  margin: 0;
  padding: 0px 0px 0px 30px;
  display: inline-block;
}

.white-logo {
  font-family: 'Merienda One', cursive !important;
  color: #fff;
  font: bold !important;
  text-decoration: underline !important;
}

.white-logo span {
  color: #087059 !important;
}

.colored-logo {
  font-family: 'Merienda One', cursive !important;
  color: #087059 !important;
  font: bold !important;
  text-decoration: underline !important;
}

.colored-logo span {
  color: #000 !important;
}

.navigations {
  float: right !important;
}

.navigations ul {
  margin: 0px;
  padding: 0px 30px 0px 0px;
  list-style: none;
  display: flex;
  float: right !important;
}

.navigations ul li {
  list-style: none !important;
}

.navLinks {
  display: block;
  color: inherit;
  padding: 0px 12px;
  text-decoration: none;
  text-transform: uppercase;
  line-height: 30px;
  font-weight: bold;
  font-size: 14px;
}

.navLinks:hover {
  color: #087059;
  text-decoration: none;
}

.underline:after {
  display: block;
  content: '';
  border-bottom: solid 3px #087059;
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
  transform-origin: 100% 50%;
}

.underline:hover:after, .underline:focus:after, .underline:active:after {
  transform: scaleX(1);
  transform-origin: 0 50%;
}

