/* Landing Page */

.main-banner {
  background: url(./Static/Images/strange.jpg) no-repeat center center;
  padding: 0;
  -webkit-background-size: cover;
  background-size: cover;
  -moz-background-size: cover;
  background-attachment: fixed;
}

.main-banner .overlay {
  /* background-color: rgba(3, 3, 3, 0.4); */
  background-color: rgba(275, 275, 275, 0.97);
  min-height: 700px;
