/* .about-us-banner {} */

.text-align {
  text-align: center !important;
}

.text-align, .title:after {
  margin: 20px auto 0px;
}

.title:after {
  content: "";
  display: block;
  height: 4px;
  width: 50px;
  background-color: #087059;
  margin-top: 20px;
}

.img-responsive {
  width: 100%;
  height: auto;
  display: block;
}

/* Services */

.services {
  padding: 45px 25px;
  height: 430px;
  text-align: center !important;
  background: #eeeef1;
  color: black;
  cursor: pointer;
  margin-bottom: 30px;
  -webkit-transition: all 0.4s ease 0s;
  -moz-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

.services:hover {
  background: white !important;
  /* color: #087059 !important; */
  box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.2), 0 16px 50px 0 rgba(0, 0, 0, 0.19);
}

.services:hover>.service-title {
  color: #087059 !important;
}

/* Skills */

.skill {
  width: 100%;
  height: 120px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #d8d6d6;
  color: #d8d6d6 !important;
  font-weight: bold;
  box-shadow: none;
  -webkit-transition: all 0.4s ease 0s;
  -moz-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

.skill:hover {
  background: white !important;
  color: #000 !important;
  box-shadow: 0 4px 12px 0 rgba(221, 221, 221, 0.7), 0 10px 30px 0 rgba(221, 221, 221, 0.5);
}

.skill .label {
  margin-top: 5;
  color: inherit;
  font-weight: bolder;
  font-size: 1.2em;
  font-family: "Times New Roman", Times, serif;
}

.skill:hover>#html {
  color: red !important;
}

.skill:hover>#css {
  color: blue !important;
}

.skill:hover>#js {
  color: yellow !important;
}

.skill:hover>#react {
  color: rgb(109, 197, 212) !important;
}

.skill:hover>#flutter {
  color: blue !important;
}

.skill:hover>#node {
  color: green !important;
}

.skill:hover>#mongo {
  color: green !important;
}

.skill:hover>#firebase {
  color: orange !important;
}

